const bgm = uni.createInnerAudioContext();
bgm.src = 'http://img.hazer.top/music.mp3'
bgm.loop = true;

var music = {
    //mute 
    playBgm({mute=false}){
		
        if (!bgm) return;
        if(mute){
            bgm.pause()
        }else{
			console.log("aaa");
            bgm.play()
        }
        
        bgm.onPause(()=>{
            console.log('');
        })
        bgm.onPlay(() => {
            console.log('#######');
        })
        bgm.onError((res) => {
            console.log(res)
        })
    }
}
module.exports = music